.booking-list {
    margin: 0 auto;
    padding: 0;
    list-style: none;
    width: 50rem;
    max-width: 90%;
}

.booking-item {
    margin: 0.5rem 0;
    padding: 0.5rem;
    border: 2px solid #01d1d1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}